export class PopProvider extends Component {
	state = {
		amount: 6,
		order: "tequenio",
		clients: [
			{ id: 1, name: "javier" },
			{ id: 2, name: "raul" },
			{ id: 3, name: "marcos" },
			{ id: 4, name: "princesa" },
			{ id: 5, name: "martina" },
			{ id: 6, name: "pericles" },
			{ id: 7, name: "antonio" },
			{ id: 8, name: "milagritos josefina" }
		]
	};

	addUser = user => {
		const newClients = Array.from(this.state.clients);
		const newClient = {
			name: user,
			id: uniqueId()
		};
		newClients.push(newClient);
		this.setState({ clients: newClients });
	};

	render() {
		return (
			<PopContext.Provider
				value={{
					state: this.state,
					increaseOrder: () => this.setState({ amount: this.state.amount + 1 }),
					//increaseItem: (type) => this.setState({ type: this.state.type + 1 }), // interesting...
					addUser: this.addUser
				}}
			>
				{this.props.children}
			</PopContext.Provider>
		);
	}
}

