import React, { Component } from "react";

import "./Results.css";

import { PopContext } from "./PopContext";

class Results extends Component {
	render() {
		return (
			<React.Fragment>
				<div className="results">
					<div className="summary">
						<div className="summary-detail">
							<div className="dozen-wrapper">
								<div className="summary-detail-desc">DOCENAS</div>
								<span>4 mas para 3 docenas</span>
							</div>
							<div className="summary-detail-quantity">2</div>
						</div>
						<div className="summary-detail">
							<div className="summary-detail-desc">TEQUEBACON</div>
							<div className="summary-detail-quantity">5</div>
						</div>
						<div className="summary-detail">
							<div className="summary-detail-desc">TEQUEYOYO</div>
							<div className="summary-detail-quantity">30</div>
						</div>
						<div className="summary-detail">
							<div className="summary-detail-desc">PAPITAS</div>
							<div className="summary-detail-quantity">8</div>
						</div>
						<div className="summary-detail">
							<div className="summary-detail-desc">MANDOCAS</div>
							<div className="summary-detail-quantity">5</div>
						</div>
						<div className="summary-detail">
							<div className="summary-detail-desc">TEQUEBACON</div>
							<div className="summary-detail-quantity">6</div>
						</div>
					</div>
					<PopContext.Consumer>
						{context => (
							<React.Fragment>
								<div className="btn-container">
									<a
										className="btn-whatsapp"
										href="https://wa.me/5491132379456?text=Hola%20tequepops%20queria%20pedirte%20lo%20siguiente:"
									>
										WHATSAPP
									</a>
								</div>
								<p>
									i want {context.state.amount} {context.state.order} please!
								</p>
							</React.Fragment>
						)}
					</PopContext.Consumer>
				</div>
			</React.Fragment>
		);
	}
}

export default Results;
